import React from 'react'
import './notifications.scss'
import Mid from '../theMid/Mid'
import Right from '../../pages/rigthPane/Right'
import SettingsIcon from '../../asiste/settingsIcon/SettingsIcon'
import { Link, Outlet } from 'react-router-dom'


function Notifications() {
  return (
    <div className='home'>
        <div className="mid">
            <div className="notifications">
                <div className="top__notifications">
                 <h2> Notifications</h2>
                  <SettingsIcon/>
                </div>
            </div>
            <div className="links__notifications">
              <Link to="">
                  <div className="box__links__notifications">
                      <p>All</p>
                  </div>
              </Link>
              <Link to="verified">
                  <div className="box__links__notifications">
                      <p>verified</p>
                  </div>
              </Link>
              <Link to="mentions">
                  <div className="box__links__notifications">
                      <p>mentions</p>
                  </div>
              </Link>
            </div>
        <Outlet/>
        </div>
        <Right/>
    </div>
  )
}

export default Notifications
