import React, { useEffect, useState,CSSProperties  } from 'react'
import './profileMid.scss'
import TopNav from '../../../components/theMid/topNav/TopNav'
import Personal from '../../../components/theMid/Personal/Personal'
import { Link, Outlet, Route, Routes , useParams } from 'react-router-dom'
import image from '../../../images/unnamed.jpg'
import ProfileLinks from '../../../components/profile/profile-links/ProfileLinks'
import Test from '../../../components/Test'
import WhoFollow from '../../../components/profile/Who-to-follow/WhoFollow'
import Replies from '../../../components/profile/replies/Replies'
import Highlights from '../../../components/profile/highlights/Highlights'
import axios from 'axios'
import ClipLoader from "react-spinners/ClipLoader";
import PuffLoader from "react-spinners/PuffLoader";

function ProfileMid() {

  let params = useParams();

  const [userData,setUserData] = useState([]);

  let [loading, setLoading] = useState(true);



useEffect(()=> {



  let username = params.userName
  console.log(username);
        
        axios.post('http://localhost:4000/specificuser',{username} )
        // .then(res => console.log(res))
        .then(res => setUserData(res.data))
        .catch(e => console.log(e))
     

      axios.get('http://localhost:4000/specificuser' )
      .then(res => console.log(res.data.birthday))
      .catch(e => console.log(e))


      
},[])

console.log(userData);







  return (

    <>
          <div className='mid-profile'>
      {
         userData.length !== 0 ? ( <>
          <div className="profile__top">
            <span className="icon">
              <Link to="/">
                <svg width='30px' height='30px' fill='#fff'>
                  <g>
                  <path d="M7.414 13l5.043 5.04-1.414 1.42L3.586 12l7.457-7.46 1.414 1.42L7.414 11H21v2H7.414z"></path>
                  </g>
                </svg>
              </Link>
            </span>
            <div className="profile__data">
              <h3>{userData.name}</h3>
              {/* <h3>{data[0].username}</h3> */}
              <p>0 posts</p>
            </div>
          </div>
          <div className="profile-edit">
            <div className="main-image">
              <img src={image} alt="" />
            </div>
            <div className="Edit-photo">
              <div className="photo">
                <img src={image} alt="" />
              </div>
              <div className="box">
                 <span class="icon">
                  <Link to="messages">
                  <svg width="30px" height="30px"  viewBox="0 0 35 35">
                    <title>more</title>
                    <g>
                    <path d="M3 12c0-1.1.9-2 2-2s2 .9 2 2-.9 2-2 2-2-.9-2-2zm9 2c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2zm7 0c1.1 0 2-.9 2-2s-.9-2-2-2-2 .9-2 2 .9 2 2 2z"></path>
                    </g>
                  </svg>
                  </Link>
                 </span>
                 <span class="icon">
                  <svg width="30px" height="30px"  viewBox="0 0 35 35">
                  <title>message</title>
                    <g>
                    <path d="M1.998 5.5c0-1.381 1.119-2.5 2.5-2.5h15c1.381 0 2.5 1.119 2.5 2.5v13c0 1.381-1.119 2.5-2.5 2.5h-15c-1.381 0-2.5-1.119-2.5-2.5v-13zm2.5-.5c-.276 0-.5.224-.5.5v2.764l8 3.638 8-3.636V5.5c0-.276-.224-.5-.5-.5h-15zm15.5 5.463l-8 3.636-8-3.638V18.5c0 .276.224.5.5.5h15c.276 0 .5-.224.5-.5v-8.037z"></path>
                    </g>
                  </svg>
                 </span>
              <div className="bottom-follow">follow</div>
              </div>
            </div>
          </div>
          <div className="profile-name">
              <h3>{userData.name}</h3>
               <p>{userData.email}</p>
               {/* <h3>{data[0].username}</h3>  */}
              {/* <p>{data[0].email}</p> */} 
          </div>
          <div className="Joined">
            <span className='icon'>
              <svg  width="30px" height="30px" fill='#71767b' viewBox='0 0 30 30'>
                <g>
                <path d="M7 4V3h2v1h6V3h2v1h1.5C19.89 4 21 5.12 21 6.5v12c0 1.38-1.11 2.5-2.5 2.5h-13C4.12 21 3 19.88 3 18.5v-12C3 5.12 4.12 4 5.5 4H7zm0 2H5.5c-.27 0-.5.22-.5.5v12c0 .28.23.5.5.5h13c.28 0 .5-.22.5-.5v-12c0-.28-.22-.5-.5-.5H17v1h-2V6H9v1H7V6zm0 6h2v-2H7v2zm0 4h2v-2H7v2zm4-4h2v-2h-2v2zm0 4h2v-2h-2v2zm4-4h2v-2h-2v2z"></path>
                </g>
              </svg>
            </span>
            <p>Joined November 2023</p>
            {/* <p>{data[0].bio}</p> */}
          </div>
          <div className="Follow">
              <a href="#"> <span>3</span> Following</a>
              <a href="#"> <span>0</span> Followers</a>
          </div>
          <ProfileLinks/>
          <Outlet/>
            
         </>) :  <div className='Waiting'>
                      <ClipLoader
                          color="#ffffff"
                          loading={loading}
                          size={50}
                          aria-label="Loading Spinner"
                          data-testid="loader"
                    />
                </div> 
      }
          </div>



    </>
  )
}

export default ProfileMid
