import './log-in-twitter.scss';
import  { useRef, useState } from 'react';
import { BiX } from "react-icons/bi";
import logo from "../../images/new-2023-twitter-logo-x-icon-design_1017-45418.png";
import {dataMonth} from './Date'
import {Days} from './Date'
import { years } from './Date';
import axios from 'axios';
import Cookies from 'cookie-universal';










function LogInTwitter() {


    const inputEmail = useRef();
    const inputPassword = useRef();





    const [emailInv, setEmailInv] = useState('');
    const [error, setError] = useState(null);
  
    function isValidEmail(emailInv) {
      return /\S+@\S+\.\S+/.test(emailInv);
    }
  
    
    const handleChange = event => {
      if (!isValidEmail(event.target.value)) {
        setError('Email is invalid');
      } else {
        setError(null);
      }
  
      setEmail(event.target.value);
      setEmailInv(event.target.value);
    };





    const [modal, setModal] = useState(false);
    const toggleModal = () => {
      setModal(!modal);
    };

    const [enterPassword, setEnterPassword] = useState(false);
    const modalPassword = () => {
        setEnterPassword(!enterPassword);
    };


    const [EmailWrong,setEmailWrong] = useState(false)
    const [pasWrong,setPasWrong] = useState(false)
    const toggleWrongPas = () => {
        setPasWrong(!pasWrong)
        console.log(pasWrong);
    }

    const [emailLogin, setEmailLogin] = useState();
    const [passwordLogin, setPasswordLogin] = useState();

    const [inputError,setInputError] = useState(false);
        // const toggleInputError = () => {
        //     setInputError(!inputError)
        //     console.log(inputError);
        // }


    const [Name, setName] = useState();
    const [email, setEmail] = useState();
    const [password, setPassword] = useState();
    const [month, setMonth] = useState();
    const [day, setDay] = useState();
    const [year, setYear] = useState();
    const [birthday, setBirthday] = useState();


    //wrong scenario login
     const  scenarioEmail = "sayed@ss.com";
     const  scenarioPassword = 1020304050;

    
  
     async  function handleFormLogin  (e)  {
        e.preventDefault()

        const cookies = new Cookies();
        cookies.set('cookiesEmail',emailLogin);
        let cookiesEmail = cookies.get('cookiesEmail')

        
        
        console.log(emailLogin);
        console.log(passwordLogin);
        console.log(cookiesEmail);


      
        
        if (inputEmail.current.value != "" && inputEmail.current.value != "")  {
            setPasWrong('')
        }

        if (!inputEmail.current.value == "" && !inputEmail.current.value == "" ) {
            setInputError(false)

            // await   axios.post('http://localhost:4000/login', {emailLogin,passwordLogin,cookiesEmail})
            // .then ((res) => {
            //     if (res.data == "loged in") {
            //         window.location ='/home'
            //         console.log(res.data)
            //     } else if (res.data == 'please signup') {
            //         setPasWrong('please signup')
            //         console.log(res.data)
            //     }  else if (res.data == 'invalid password') {
            //         setPasWrong('invalid password')
            //         modalPassword()
            //         console.log(res.data)
            //     }
            // } )
            // .catch(e => console.log(e))

            if (inputEmail.current.value === scenarioEmail && parseInt(inputPassword.current.value) === scenarioPassword ) {
                window.location ='/home'
                console.log("e");
            }

            if (inputEmail.current.value !== scenarioEmail) {
                console.log("it wrong Email sayed");
                let errorInputEmail = inputEmail.current;
                errorInputEmail.style.borderColor = '#f02849';
                setEmailWrong('invalid email Please enter a valid email')
            } else if (inputEmail.current.value === scenarioEmail) {
                const errorInputEmail = inputEmail.current;
                errorInputEmail.style.borderColor = '#0866ff';
                setEmailWrong('')
            } 
            
            if (inputEmail.current.value === scenarioEmail &&  parseInt(passwordLogin) !== scenarioPassword) {
                modalPassword()
                console.log("password wrong sayed");
            } else if (inputEmail.current.value === scenarioEmail && parseInt(passwordLogin) === scenarioPassword ) {
                window.location ='/home'
            }

        } else if (inputEmail.current.value == "" && inputEmail.current.value == "") {
            setPasWrong('please enter your data')
            setInputError(true)
        } 
         } 




      async  function handleFormCreate (e)  {
            e.preventDefault()
            console.log(Name);
            console.log(email);
            console.log(password);
            console.log(birthday);

            if(error === null && Name && email && password && birthday ) {
           await axios.post('http://localhost:3600/signup', {Name,email,password,birthday})
           // .then(res => console.log(res.statusText))
           .then(res => res.statusText ?   window.location ='/home' : console.log("no"))
        //    .then(res => res.statusText ?   console.log(res.data) : console.log("no"))
           .catch(e => console.log(e))
          }
            }
            


        
        

    


  return (
        <div className='login'>
          <div className="container">
        <div className="logo">
                <img src={logo} alt="" /> 
        </div> 
         <div className="inputs__login">
            <div className="main-box__input">
            <form action="#" onSubmit={handleFormLogin} >
            <input type="email"  placeholder='Email address new' className={`${inputError ? "error" : ""}`}  ref={inputEmail} onChange={(e)=> setEmailLogin(e.target.value)} />
                <span className={`pas-wrong ${EmailWrong != false ? "Active" : ""}`}> {EmailWrong}</span>
            <input type="password" placeholder='password' className={`${inputError ? "error" : ""}`}  ref={inputPassword} onChange={(e)=> setPasswordLogin(e.target.value)}/>
             <p className={`pas-wrong ${pasWrong ? "Active" : ""}`}> {pasWrong}</p>
            <input type="submit" value="Log in" />
            <p  onClick={toggleWrongPas}><a href="#">Forgotten password?</a></p>
            <div className="line"></div>
            </form>
            <bottom  onClick={toggleModal} ><a href="#">Create new account</a></bottom>
            </div>
        <p className='link-create'><a href="#">Create a Page</a> for a celebrity, brand or business. </p>
        </div>
        
        </div>

        {/* ///////////// */}
        


        {modal && (
            <form action='#' onSubmit={handleFormCreate}>
            <div className="modal">
            <div onClick={toggleModal} className="overlay"></div>
            <div className="registration">
                <div className="dis">
                <span className="close-modal" onClick={toggleModal}>
                <BiX/>
                </span>
                <div className="text__dis">
                    <p>step 1 of 5</p>
                </div>
                </div>
                <div className="input__registration">
                    <div className="box">
                    <h3>Create your account</h3>
                        <input type="text" placeholder='name' onChange={e => setName(e.target.value)}/>
                        {/* <input type="email" placeholder='Email' onChange={e => setEmail(e.target.value)}/> */}
                        <input placeholder='Email' name="email" value={email} onChange={handleChange}  className={`${error ? "error" : ""}`} />
                        <input type="password" placeholder='password' onChange={e => setPassword(e.target.value)}/>
                    <p className='title'>Date of birth</p>
                    <p className="dis2">
                    This will not be shown publicly. Confirm your own age, even if this account is for a business, a pet, or something else.
                    </p>
                    <div className="inputs-select">
                        <select id='month' onChange={e => setMonth(e.target.value)}>
                            {dataMonth.map(option => (
                                <option value={option.value} key={option.id}>  {option.label} </option>
                            ))}
                        </select>
                        <select id='day' name='day' onChange={e => setDay(e.target.value)}>
                            <option value="day">Day</option>
                            {Days.map(day => (
                                
                                <option value={day.value} key={day.value}> {day.value} </option>
                            ))}
                        </select>
                        <select id='year' onChange={e =>  setBirthday(`${e.target.value}-${month}-${day}`)}>
                            <option value="Year"> Year</option>
                            {
                                years.map(year => (
                                    <option value={year} key={year}>{year}</option>
                                ))
                            }
                        </select>
                    </div>
                    </div>
                </div>
                    <input className={`bottom `} type="submit" value = "Next" />
                    {/* <input className='bottom' type="submit" value = "Next" /> */}
    
            
            </div>
            </div>
            </form>
        )}


        {enterPassword && (
            <form action='#' onSubmit={handleFormLogin}>
            <div className="modal">
            <div onClick={modalPassword} className="overlay"></div>
            <div className="password-agin">
                <div className="dis">
                <span className="close-modal" onClick={modalPassword}>
                <BiX/>
                </span>
                </div>
                <div className="log-as">
                    <h2>Log in as Sayed Ayman</h2>
                    <input type="password" placeholder='password' onChange={e =>setPasswordLogin(e.target.value)} />
                    <p>The password that you've entered is incorrect. Forgotten password?</p>
                    {/* <div className="login-input"> <span>Log in</span></div> */}
                    <input onSubmit={handleFormLogin} type='submit' value="Log in"/> 
                </div>
    
            
            </div>
            </div>
            </form>
          )}
        
    </div>
  )
}

export default LogInTwitter
