import React from 'react'
import './VerifiedNotifications.scss'
import image from '../../../images/verification-check-400x200.v1.46c9cb39.png'

function VerifiedNotifications() {
  return (
    <div className='verified'>
       <div className="box__verified">
           <img src={image} alt="" />
           <h1>Nothing to see here  yet</h1>
           <p>
               Likes, mentions, reposts, and a whole lot more  when it comes from a verified account, youll find it here.
               <a href="#">Learn more</a>
           </p>
       </div>
    </div>
  )
}

export default VerifiedNotifications
